import React from 'react'
import styled, { css } from 'styled-components'
import {connect, RootStateOrAny} from 'react-redux'

import Inputs from '../components/Inputs'
import Header from '../components/Header'
import Results from '../components/Results'
import {BannerContainerType} from '../types/componentTypes'


const BannerContainer = styled.div`
    width: 100%;
    display:flex;
    justify-content: ${(props: BannerContainerType)=>props.text == '' ? 'center' : 'flex-start'};
   
`
const MainPage: React.FC = (props:any) =>{

    return(
        <div>
            <BannerContainer text={props.inputFieldReducer.text}>
                <div >
                    <Header text='Search users or repositories below'/>
                    <Inputs />
                </div>
            </BannerContainer>
            
            <Results />
        </div>
    )
}

const mapStateToProps = (state:RootStateOrAny) =>{
    return{
        inputFieldReducer: state.inputFieldReducer,
    }
}

export default connect(mapStateToProps,null)(MainPage) ;